@import 'normalize';
@import 'variables';
@import 'mixins';
@import 'slick';

*
{
    box-sizing: border-box;
    transition: all .3s ease-in-out;
    text-decoration: none;
}
html
{
    font: 400 10px $text-ff;
}

h1
{
    text-transform: uppercase;
    color: $main-c;
    font-size: 8.2rem;
    margin: 0;
    @media  screen and (max-width:768px) 
    {
        font-size: 6rem;
    }
    @media screen and (max-width: 576px)
    {
        font-size: 5rem;
    }
}
h2
{
    font-weight: 700;
    font-size: 3.6rem;
    text-transform: uppercase;
    text-align: center; 
    @media screen and (max-width: 992px)
    {
        font-size: 3.8rem;
    }
}
h3
{
    font-weight: 700;
    font-size: 3.6rem;
    color: $main-c;
    margin: 0;
    @media screen and (max-width: 768px)
    {
      text-align: center;
    }
}
h4
{
    font-size: 2.5rem;
    color: $main-c;
    margin: 30px 0;
}
.container
{
    max-width: 1200px;
    padding: 20px;
    margin: 0 auto;
}
.img_wrap
{
    position: relative;
    padding-top: 75%;
    overflow: hidden;
    width: 100%;
    @media screen and (max-width: 992px)
    {
        width: 80%;
        margin: 0 auto;
        padding-top: 50%;
    }
    @media screen and (max-width: 576px)
    {
        width: 100%;
        padding-top: 80%;
    }
}
.img
{
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.row
{
    display: flex;
    flex-wrap: wrap;
}
.col
{
    padding: 40px;
    
    &--about
    {
        padding: 15px;
    }
}
.col-1-2
{
    width: 50%;
    &--overlay
    {
        background-color: rgba(37, 41, 54, 0.6);
        position: relative;
        padding-top: 55%;
        margin: 0 0 0 auto;
    }
    @media screen and (max-width: 992px)
    {
        width: 100%;
    }
}
.switch--1
{
    @media screen and (max-width: 992px)
    {
        order: 2;
    }
}
#header
{
    @media screen and (max-width: 992px)
    {
        display: none;
    }
}
.btn
{
    text-align: center;
    background-color: transparent;
    padding: 20px 65px;
    margin: 35px 0;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    font-size: 2rem;
    color: $or-c;
    outline: none;
    border: $b-width solid $or-c;
    @media screen and (max-width: 992px)
    {
        display: block;
        margin: 35px auto;
        font-size: 2.4rem;
    }
    @media screen and (max-width: 768px)
    {
        padding: 10px 30px;
        font-size: 2rem;
    }
    @include hover
    {
        border-color: transparent;
        background-color: $or-c;
        color: $main-c;
        cursor: pointer;
    }
    &--header
    {
        background-color: $or-c;
        color: $main-c;
        border: $b-width solid transparent;
        @include hover
        {
            border-color: $or-c;
            background-color: transparent;
            color: $or-c;
        }
        @media screen and (max-width: 576px)
        {
            margin: 10px auto;
        }
    }
    &--contacts
    {
        margin: 0;
        padding: 13px 65px;
        width: 100%;
        background-color: $main-c;
        border: $b-width solid transparent;
        @include hover
        {
            background-color: transparent;
            border-color: $main-c;
        }
        
    } 
}
.header
{
    font-size: 3rem;
    color: $main-c;
    p 
    {
        font-family: 'Lato', sans-serif;
        @media screen and (max-width: 576px)
        {
            display: none;
        }
    }
    @media  screen and (max-width:768px) 
    {
        font-size: 2rem;
    }   
}
.navigation
{
    padding: 10px 50px;
    background-color: $main-c;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: $bl-tt;
    @media screen and (max-width: 576px)
    {
        padding: 10px 30px;
    }
    &__logo
    {
        
        text-transform: uppercase;
        color: $logo;
        font-size: 3.4rem;
        font-weight: 700;
        letter-spacing: 0.6rem;
        @include hover
        {
            color: $or-c;
        }
        
    }
    &__elems
    {
        list-style-type: none;
        display: flex;
        justify-content: center;
        font-size: 1.8rem;
        color: $bl-tt;
        @media screen and (max-width: 992px)
        {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            position: fixed;
            padding: 15% 0;
            top:0;
            left: 0;
            opacity: 0;
            width: 100%;
            min-height: 100vh;
            background-color: $or-c;
            font-size: 2.4rem;
            text-align: center;
            transform: translate(0,0) scale(0);
            z-index: 15;
            margin: 0;
            &.open
            {
               opacity: 1;
               transform: scale(1);
               transition:all .5s ease-in-out;    
            }       
        }

    }
    &__elem
    {
        padding: 10px 20px;
        
    }
    &__link
    {
        text-decoration: none;
        color: inherit;
        @include hover
        {
            color: $or-c;
        }
        @media screen and (max-width: 992px)
        {
            border-bottom: $b-width solid $bl-tt;
            padding: 10px 0;
            @include hover
            {
                color: $main-c;
                border-color: $main-c;
            }
        }
    }
    &__number
    {
        font-size: 2rem;
        color: inherit;
        font-weight: 700;
        @media screen and (max-width: 576px)
        {
            font-size: 1.4rem;
            margin-right: 8px;
        }
        .fas
        {
            color: $or-c;
            margin: 0 5px;
        }
        @include hover
        {
            color: $or-c;
            .fas
            {
                color: $logo;
            }   
        }
    }
    &__burger
    {
        display: none;
        margin: 2px;
        margin-right: 12px;
        @media screen and (max-width: 992px)
        {
            display: block;
            color: $logo;
            font-size: 2.3rem;   
            position: fixed;
            top: 0;
            right: 0;
            transform: translate(0, 50%);
            z-index: 16;
        } 
        #cross
        {
            display: none;
        }
        &.open
        {
            #hamburger
            {
                display: none;
            }
            #cross
            {
                display: block;  
            }
        }
    }
}
.slider
{
    position: relative;
    &__slide
    {
        background-repeat: no-repeat;
        background-size: cover;
    }
    &__content
    {
        position: absolute;
        padding: 0 60px;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        @media screen and (max-width: 576px)
        {
            padding: 0 10px;
            text-align: center;
        }
    }
    &__dots
    {
        list-style-type: none;
        display: flex;
        position: absolute;
        bottom: 0;
        right: 0;
        transform: translate(-50%, -50%);
        @media screen and (max-width: 576px)
        {
            transform: translate(-40%, 50%);
        }
        li 
        {
            display: inline-block;
            margin: 0 5px;
            button 
            {
                font-size: 0;
                width: 45px;
                height: 3px;
                background-color: $main-c;
                opacity: 0.7;
                outline: none;
                border: none;
                @include hover
                {
                    opacity: 1;
                    cursor: pointer;
                }
            }
            &.slick-active button
            {  
                opacity: 1;
            }
        }
    }
}
.contacts
{
    &__form
    {
        margin-top: 20px;
    }

    &__items
    {
        list-style-type: none;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-end;
        padding: 0;
        @media screen and (max-width: 992px)
        {
            justify-content: center;
        }
    }
    &__item
    {
        display: block;
        width: calc(50% - 2 * 8px);
        margin: 5px 8px;
        @media screen and (max-width: 576px)
        {
            width: calc(100% - 2 * 8px);
        }
        input
        {
            width: 100%;
            padding: 15px 25px;
            background-color: rgba(255, 255,255, 0.45);
            text-transform: uppercase;
            font-weight: 500;
            font-size: 1.6rem;  
            outline: none;
            border: $b-width solid transparent;
            &:focus
            {
                background-color: transparent;
                border-color: $main-c;
            }
        }
        ::placeholder
        {
            color: $main-c;
        }
    } 
    &__info
    {
        width: 80%;
        margin: 0 auto;
        @media screen and (max-width: 992px)
        {
            text-align: center;
        }
        @media screen and (max-width: 992px)
        {
            width: 100%;
        }
    }
    &__quote
    {
        font-weight: 500;
        font-size: 3rem;
        font-style: italic;
        margin: 0;
        @media screen and (max-width: 992px)
        {
            font-size: 2.4rem;
        }
    }
    p 
    {
        font-size: 1.8rem;
        color: $grey;
        margin-top: 40px;
        width: 75%;
        line-height: 2.2rem;
        @media screen and (max-width: 992px)
        {
            margin: 40px auto auto auto;
        }
        @media screen and (max-width: 992px)
        {
            font-size: 2.2rem;
        }
        @media screen and (max-width: 992px)
        {
            margin: 20px 0 0 0;
            width: 100%;
        }
    }
}
.about
{
    p
    {
        margin: 0 0 15px 0;
        font-family: 'Lato', sans-serif;
        font-size: 1.8rem;
        @media screen and (max-width: 992px)
        {
            text-align: center;
        }
        @media screen and (max-width: 992px)
        {
            font-size: 2.2rem;
        }
    }
}
.footer
{
    background-color: $footer;
    &__elems
    {
        list-style-type: none;
        padding: 70px 0 150px 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
        @media screen and (max-width: 992px)
        {
            justify-content: center;    
            padding: 50px 0 100px 0;
        }
    }
    &__elem
    {
        display: flex;
        flex-direction: column;
        margin: 0 20px;
        width: calc(20% - 2 * 20px);
        font-size: 1.8rem;
        font-family: 'Lato', sans-serif;
        font-weight: 300;
        line-height: 2.6rem;
        color: $foot-tt;
        @media screen and (max-width: 992px)
        {
            width: calc(50% - 2 * 20px) !important;
            font-size: 2rem;
        }
        @media screen and (max-width: 992px)
        {
            font-size: 1.6rem;
            width: calc(100% - 2 * 20px) !important;
        }
        p 
        {
            position: relative;
            padding-left: 10px;
            &::before
            {
                content: '';
                display: block;
                position: absolute;
                width: $b-width;
                height: 100%;
                top: 0;
                left: 0;
                background-color: $or-c;
            }
            &#nobefore
            {
                padding: 0;
                &::before
                {
                    display: none;
                }
            }
        }
        &--logo
        {
            h4
            {
                text-transform: uppercase;
                font-size: 3rem;
            }
            width: calc(35% - 2 * 20px);
        }
        &--socials
        {
            width: calc(25% - 2 * 20px);
        }
        a 
        {
            color: inherit;
            font-size: inherit;
            font-weight: 400;
            @media screen and (max-width: 992px)
            {
                display: block;
            }
        }
    }
    &__socials
    {
        list-style-type: none;
        padding: 0;
        display: flex;
        flex-direction: row;
    }
    &__social
    {
        margin: 0 8px;
        span 
        {
            display: inline-block;
            width: 48px;
            height: 48px;
            @include hover
            {
                box-shadow: 0px 0px 8px $or-c;
            }
        }
    }
}

.copyright
{
    border-top: 1px solid $foot-tt;
    padding: 60px 0 30px 0;
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    font-size: 1.8rem;
    color: $foot-tt;
    text-align: center;
    @media screen and (max-width: 992px)
    {
        font-size: 2rem;
    }
}